* {
  font-family: 'Fira Sans', sans-serif;
}
#hero {
  > .jumbotron {
    background-color: unset;
  }

  .information {
    width: 200px;
    margin: 0 auto;
    text-align: left;

    h4 {
      font-weight: bold;
      margin-bottom: 0;
    }
    span {
      color: #9E9E9E;
    }
  }
}
#categories {
  text-align: center;
  margin: 3em 0;

  > a.category {
    color: #212121;
    background-color: #FFC107;
    padding: 8px 16px;
    border-radius: 1em;
    margin-right: 8px;

    &:hover {
      text-decoration: none;
    } 
  }
}

.galleries-wrap {
  width: 100%;
  line-height: 0;
   
  -webkit-column-count: 5;
  -webkit-column-gap:   0px;
  -moz-column-count:    5;
  -moz-column-gap:      0px;
  column-count:         5;
  column-gap:           0px; 

  img {
    transition: 1s ease-in-out;
    margin: 4px;

    width: calc(100% - 8px);
    height: auto !important;
  }
}

@media (max-width: 1200px) {
  #photos {
  -moz-column-count:    4;
  -webkit-column-count: 4;
  column-count:         4;
  }
}
@media (max-width: 1000px) {
  #photos {
  -moz-column-count:    3;
  -webkit-column-count: 3;
  column-count:         3;
  }
}
@media (max-width: 800px) {
  #photos {
  -moz-column-count:    2;
  -webkit-column-count: 2;
  column-count:         2;
  }
}
@media (max-width: 400px) {
  #photos {
  -moz-column-count:    1;
  -webkit-column-count: 1;
  column-count:         1;
  }
}